import React, { Component } from 'react';
import { connect } from 'react-redux';
import { 
    ListGroupItem, 
    Button, 
    Input, 
    InputGroup, 
    InputGroupAddon, 
} from 'reactstrap';

class CustomItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isHover: false,
            isEditing: false,
            newName: this.props.value.name
        }
    }

    mouseOver() {
        this.setState({isHover: true});
    }

    mouseOut() {
        this.setState({isHover: false});
    }

    rename() {
        this.setState({
            isEditing: false
        });
        this.props.update(this.state.newName);
    }

    render() {
        const { value, isCurrent } = this.props;
        const { isHover, isEditing, newName } = this.state;

        let content;
        if (isEditing) {
            content = <InputGroup>
                        <Input value={newName} onChange={(e)=>this.setState({newName: e.target.value})}/>
                        <InputGroupAddon addonType="append">
                            <Button color="primary" onClick={this.rename.bind(this)}>Ok</Button>
                        </InputGroupAddon>
                    </InputGroup>
        }
        else {
            content = <span 
                onClick={this.props.fouse.bind(this)}
                style={{flex: 1, cursor: 'pointer'}}>
                {value.name}
                </span>
        }

        return (
            <ListGroupItem onMouseOver={this.mouseOver.bind(this)} 
                onMouseOut={this.mouseOut.bind(this)}
                style={{
                    display: 'flex',
                    flexDirection: 'row',
                    background: isCurrent?'#cbd6e9':(isHover?'#ccc':null)
                }}
            >
                {content}
                <span style={{...styles.icon, display:isHover&&!isEditing?'inherit':'none'}}
                    onClick={this.props.delete.bind(this)}
                >
                    del
                </span>
                <span style={{...styles.icon, display:isHover&&!isEditing?'inherit':'none'}}
                    onClick={()=>{this.setState({isEditing: true})}}
                >
                    edit
                </span>
            </ListGroupItem>
        );
    }
}

const styles = {
    icon: {
        fontSize: 12,
        cursor: 'pointer',
        margin: 5,
    }
}

export default CustomItem;