import {
    ADD_USER,
    UP_USER,
    DEL_USER,
    SET_CURRENT_USER,
    ADD_GROUP,
    UP_GROUP,
    DEL_GROUP,
    SET_CURRENT_GROUP
} from '../action/organization';

export default function(state={
    groups: [{name: 'defaultGroup', id: 0}],
    users: [{name: 'defaultUser', id: 0, groupid: 0}],
    currentGroup: 0,
    currentUser: 0,
}, action) {
    let groups, users, id, user, group;
    switch (action.type) {
        case ADD_USER:
            id = 0;
            users = state.users.map(item => {
                if (item.id > id)
                    id = item.id;
                return item;
            });
            users.push({
                name: action.name,
                id: id+1,
                groupid: state.currentGroup
            });
            return {...state, users, currentUser: state.currentUser==null?id+1:state.currentUser};
        case UP_USER:
            users = state.users.map(item => {
                if (item.id == action.id)
                    item.name = action.name;
                return item;
            });
            return {...state, users};
        case DEL_USER:
            users = [];
            state.users.map(item => {
                if (item.id != action.id)
                    users.push(item);
            });
            if (action.id == state.currentUser) {
                user = users.find(item => item.groupid==state.currentGroup);
            }
            return {...state, users, currentUser: user?user.id:null};
        case SET_CURRENT_USER:
            return {...state, currentUser: action.id};
        case ADD_GROUP:
            id = 0;
            groups = state.groups.map(item => {
                if (item.id > id)
                    id = item.id;
                return item;
            });
            groups.push({
                name: action.name,
                id: id+1
            });
            return {...state, groups};
        case UP_GROUP:
            groups = state.groups.map(item => {
                if (item.id == action.id) 
                    item.name = action.name;
                return item;
            });
            return {...state, groups};
        case DEL_GROUP:
            groups = [];
            users = [];
            state.users.map(item => {
                if (item.groupid != action.id)
                    users.push(item);
            });
            state.groups.map(item => {
                if (item.id != action.id)
                    groups.push(item);
            });
            if (action.id == state.currentGroup) {
                group = state.groups.find(item => item.id != action.id);
                if (group)
                    user = state.users.find(item => item.groupid == group.id);
            }
            return {...state, groups, users, currentGroup: group?group.id:null, currentUser: user?user.id:null};
        case SET_CURRENT_GROUP:
            user = state.users.find(item => item.groupid==action.id);
            return {...state, currentGroup: action.id, currentUser: user?user.id:null};
        default:
            return state;
    }
}