import React, { Component } from 'react';
import { connect } from 'react-redux';
import { 
    ListGroup, 
    ListGroupItem, 
    Button, 
    Input, 
    InputGroup, 
    InputGroupAddon, 
    Modal, 
    ModalHeader, 
    ModalBody, 
    ModalFooter,
} from 'reactstrap';

import {
    addUser,
    updateUser,
    delUser,
    setCurrentUser
} from '../action/organization'
import CustomItem from './customItem';


class User extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isAdding: false,
            newUserName: ''
        };
    }

    cancleAdd() {
        this.setState({
            isAdding: false,
        });
    }

    submitAdd() {
        this.setState({
            isAdding: false,
        });
        this.props.dispatch(addUser(this.state.newUserName));
    }

    upUser(pre, now) {
        this.props.dispatch(updateUser(pre, now));
    }

    delUser(name) {
        this.props.dispatch(delUser(name));
    }

    setFouse(id) {
        this.props.dispatch(setCurrentUser(id));
    }

    render() {
        const { users, currentUser, groups, currentGroup } = this.props;
        const { isAdding, newUserName } = this.state;

        return (
            <div className="users">
                <ListGroup className="content">
                {
                    users.filter(item => item.groupid==currentGroup)
                      .map((item, index) => <CustomItem key={index}
                        value={item}
                        isCurrent={currentUser==item.id}
                        delete={this.delUser.bind(this, item.id)}
                        fouse={this.setFouse.bind(this, item.id)}
                        update={this.upUser.bind(this, item.id)}/>)
                }
                </ListGroup>
                <div className="input">
                    <Button color="primary"
                        disabled={groups.length<=0} 
                        onClick={() => this.setState({
                            isAdding: true,
                            newUserName: ''
                        })}
                        style={{width: '100%'}}>
                            Add User
                    </Button>
                </div>
                <Modal isOpen={isAdding} toggle={this.cancleAdd.bind(this)}>
                    <ModalHeader toggle={this.cancleAdd.bind(this)}>Add User</ModalHeader>
                    <ModalBody>
                        <Input placeholder="Username" 
                            onChange={(e) => this.setState({newUserName: e.target.value})}
                            value={newUserName}/>
                    </ModalBody>
                    <ModalFooter>
                        <Button onClick={this.submitAdd.bind(this)} color="primary">Submit</Button>
                    </ModalFooter>
                </Modal>
            </div>
        );
    }
}

function mapState2Props(state) {
    const { organization: { users, groups, currentUser, currentGroup } } = state;
    return {
        users,
        groups,
        currentUser,
        currentGroup
    };
}

User = connect(mapState2Props)(User);


export default User;