import React, { Component } from 'react';
import { connect } from 'react-redux';
import { 
    ListGroup, 
    ListGroupItem, 
    Button, 
    Input, 
    InputGroup, 
    InputGroupAddon, 
    Modal, 
    ModalHeader, 
    ModalBody, 
    ModalFooter  
} from 'reactstrap';

import { 
    updateGroup, 
    delGroup, 
    addGroup,
    setCurrentGroup
} from '../action/organization';
import { delMsgs } from '../action/message';
import CustomItem from './customItem';

class Group extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isAdding: false,
            newGroupName: '',
        }
    }

    cancleAdd() {
        this.setState({
            isAdding: false,
        });
    }

    submitAdd() {
        this.setState({
            isAdding: false,
        });
        this.props.dispatch(addGroup(this.state.newGroupName));
    }

    upGroup(id, name) {
        this.props.dispatch(updateGroup(id, name));
    }

    delGroup(id) {
        this.props.dispatch(delGroup(id));
        this.props.dispatch(delMsgs(this.props.currentGroup));
    }

    setFouse(id) {
        this.props.dispatch(setCurrentGroup(id));
    }

    render() {
        const { groups, currentGroup } = this.props;
        const { isAdding, newGroupName } = this.state;

        return (
            <div className="groups">
                <ListGroup className="content">
                {
                    groups.map((item, index) => <CustomItem value={item} 
                        key={index} 
                        isCurrent={currentGroup==item.id}
                        delete={this.delGroup.bind(this, item.id)}
                        fouse={this.setFouse.bind(this, item.id)}
                        update={this.upGroup.bind(this, item.id)}/>)
                }
                </ListGroup>
                <div className="input">
                    <Button color="primary" 
                        onClick={() => this.setState({isAdding: true, newGroupName: ''})}
                        style={{width: '100%'}}>
                        Add Group
                    </Button>
                </div>

                <Modal isOpen={isAdding} toggle={this.cancleAdd.bind(this)}>
                    <ModalHeader toggle={this.cancleAdd.bind(this)}>Add Group</ModalHeader>
                    <ModalBody>
                        <Input placeholder="Group name" 
                            onChange={(e) => this.setState({newGroupName: e.target.value})}
                            value={newGroupName}/>
                    </ModalBody>
                    <ModalFooter>
                        <Button onClick={this.submitAdd.bind(this)} color="primary">Submit</Button>
                    </ModalFooter>
                </Modal>
            </div>
        );
    }
}


function mapState2Props(state) {
    const { organization: { groups, currentGroup } } = state;
    return {
        groups,
        currentGroup
    };
}

Group = connect(mapState2Props)(Group);

export default Group;