export const ADD_MSG = 'ADD_MSG';
export const DEL_MSG_BY_GROUP = 'DEL_MSG_BY_GROUP';

export const addMsg = (groupid, username, content) => {
    return {
        type: ADD_MSG,
        value: {
            groupid,
            username,
            content
        }
    };
}

export const delMsgs = (groupid) => {
    return {
        type: DEL_MSG_BY_GROUP,
        groupid
    };
}