import {
    ADD_MSG,
    DEL_MSG_BY_GROUP
} from '../action/message';

export default function(state={
    messages: []
}, action) {
    let messages;
    switch (action.type) {
        case ADD_MSG:
            messages = state.messages.map(item=>item);
            messages.push(action.value);
            return {...state, messages};
        case DEL_MSG_BY_GROUP:
            messages = state.messages.filter(item => item.groupid!=action.groupid);
            return {...state, messages};
        default:
            return state;
    }
}