export const ADD_USER = 'ADD_USER';
export const DEL_USER = 'DEL_USER';
export const UP_USER = 'UP_USER';
export const SET_CURRENT_USER = 'SET_CURRENT_USER'; 

export const ADD_GROUP = 'ADD_GROUP';
export const UP_GROUP = 'UP_GROUP';
export const DEL_GROUP = 'DEL_GROUP';
export const SET_CURRENT_GROUP = 'SET_CURRENT_GROUP';


export const addUser = (name) => {
    return {
        type: ADD_USER,
        name
    };
}

export const updateUser = (id, name) => {
    return {
        type: UP_USER,
        id,
        name
    }
}

export const delUser = (id) => {
    return {
        type: DEL_USER,
        id,
    };
}

export const setCurrentUser = (id) => {
    return {
        type: SET_CURRENT_USER,
        id
    };
}


export const addGroup = (name) => {
    return {
        type: ADD_GROUP,
        name
    };
}

export const updateGroup = (id, name) => {
    return {
        type: UP_GROUP,
        id,
        name
    };
}

export const delGroup = (id) => {
    return {
        type: DEL_GROUP,
        id
    };
}

export const setCurrentGroup = (id) => {
    return {
        type: SET_CURRENT_GROUP,
        id
    };
}

