import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Button, Input, InputGroup, InputGroupAddon } from 'reactstrap';

import { addMsg } from '../action/message';

class Message extends Component {
    constructor(props) {
        super(props);
        this.state = {
            msg: ""
        };
    }

    updateMsg(e) {
        this.setState({msg: e.target.value});
    }

    sendMsg() {
        const { msg } = this.state;
        const { currentGroup, users, currentUser } = this.props;
        console.log(users, currentUser);
        let user = users.find(item => item.id==currentUser);
        if (msg.length > 0) {
            this.props.dispatch(addMsg(currentGroup, user?user.name:'default', msg));
            this.setState({msg: ''});
        }
    }

    render() {
        const { messages, currentUser, currentGroup } = this.props;
        const { msg } = this.state;

        return (
            <div className="messages">
                <div className="content" role="display">
                {
                    messages.filter(item => item.groupid==currentGroup)
                        .map((item, index) => <div key={index}>
                            <span className="msg-user">{ item.username }:</span>
                            <span className="msg-content">{ item.content }</span>
                        </div>)
                }
                </div>
                <div className="input">
                    <InputGroup>
                        <Input value={msg} 
                            placeholder="Typing here"
                            onChange={this.updateMsg.bind(this)}/>
                        <InputGroupAddon addonType="append">
                            <Button color="primary" 
                                disabled={currentUser==null}
                                onClick={this.sendMsg.bind(this)}>Send</Button>
                        </InputGroupAddon>
                    </InputGroup>
                </div>
            </div>
        );
    }
}

function mapState2Props(state) {
    const { message: { messages }, organization: { currentGroup, currentUser, users } } = state;
    return {
        messages,
        currentGroup,
        currentUser,
        users
    };
}

Message = connect(mapState2Props)(Message);

export default Message;